.mat-form-field {
  width: 100%;
}

.heading {
  color: #585858;
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: bold;
  font-family: "GothamNarrowBold", sans-serif;
}

.clrall {
  color: #0072ce !important;
  font-family: "GothamNarrowBold", sans-serif;
  cursor: pointer;
  font-weight: bold;
}

.srch {
  position: relative;
  top: -5px;
}

.newtest {
  min-width: 178px !important;
}

.completed {
  background: #0eab1c;
  height: 16px;
  width: 16px;
}

.pending {
  background: #969696;
  height: 16px;
  width: 16px;
}

.cancelled {
  background: red;
  height: 16px;
  width: 16px;
}

.processing {
  background: #ed8b00;
  height: 16px;
  width: 16px;
}

.filter-head {
  font-weight: bold;
  font-family: "GothamNarrowBold", sans-serif;
  color: #585858;

  margin-bottom: 12px;
}

/*responsive css*/

.resp,
.respfilter {
  display: none !important;
}

.applymob {
  display: none !important;
}

@media screen and (max-width: 767px) {
  .respfilter {
    padding: 0 20px 10px 20px;
    border-bottom: 1px solid #ccc;
  }

  .respfilter .filter-head {
    margin-bottom: 0;
    color: #2476ce;
  }

  .resp.clse {
    margin-right: 20px;
    text-align: right;
    font-weight: bold;
    padding-top: 8px;
    color: red;
    font-size: 16px;
  }

  .resp.clse p span {
    font-size: 15px;
  }

  .resp,
  .respfilter {
    display: block !important;
  }

  .applymob {
    display: block !important;
    margin-left: 25% !important;
  }

  .applydesk {
    display: none !important;
  }

  .desk {
    display: none !important;
  }
}

@media screen and (min-width: 767px) and (max-width: 1024px) {
  .respfilter {
    padding: 0 20px 10px 20px;
    border-bottom: 1px solid #ccc;
  }

  .respfilter .filter-head {
    margin-bottom: 0;
    color: #2476ce;
  }

  .resp.clse {
    margin-right: 20px;
    text-align: right;
    font-weight: bold;
    padding-top: 8px;
    color: red;
    font-size: 16px;
  }

  .resp.clse p span {
    font-size: 15px;
  }

  .resp,
  .respfilter {
    display: block !important;
  }

  .applymob {
    display: block !important;
    margin-left: 25% !important;
  }

  .applydesk {
    display: none !important;
  }

  .desk {
    display: none !important;
  }

  .example-sidenav {
    top: 56px;
  }
}

::ng-deep .mat-tooltip-panel {
  margin-top: 10px;
}

::ng-deep .mat-tooltip {
  background-color: #fff;
  color: #000 !important;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  -moz-box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  box-shadow: 1px 1px 1px 1px rgba(209, 209, 209, 1);
  position: relative;
}

::ng-deep .mat-tooltip::after {
  width: 0;
  height: 0;
  content: '';
  position: absolute;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-bottom: 0.5rem solid #ffffff;
}

::ng-deep .mat-tooltip.below {
  overflow: initial;
  margin-top: 1rem;
}

::ng-deep .mat-tooltip.below:after {
  top: -0.5rem;
  right: calc(50% - 0.5rem);
  transform: rotate(0);
}

span.status-info-icon {
  position: absolute;
  bottom: 10PX;
  right: -16px;
}

::ng-deep .mat-radio-label-content {
  position: relative;
}

::ng-deep .mat-select-panel.mat-primary {
  min-width: calc(85% + 64px) !important;
}

::ng-deep .mat-option-text {
  width: 175px;
}

@-moz-document url-prefix() {
  ::ng-deep .mat-select-panel {
    max-width: 238px;
  }
}